@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;800&display=swap);
body {
  margin: 0;
  font-family: "Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
  cursor: pointer;
  width: 100%;
  display: flex;
  position: fixed;
  background: #39445a;
  font-family: "Montserrat", sans-serif, Courier, monospace;
  font-size: 3vw;
  padding-bottom: 15px;
  box-shadow: 0px 1px 5px black;
  color: white;
  z-index: 100;
  justify-content: center;
  text-transform: uppercase;
}

@media (max-width: 1000px) {
  .header {
    padding-top: 15px;
    font-size: 6.4vw;
  }
}

* {
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  min-height: 100vh;
  background-color: #39445a;
  color: #fff;
  padding-top: 130px;
  padding-bottom: 75px;
}

@media (max-width: 700px) {
  .App {
    padding-top: 70px;
  }
}

.pageTitle {
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  font-family: "Montserrat", sans-serif;
  font-size: 1vw;
  padding: 4px;
  border-radius: 50px;
  color: #cecece;
}

@media (max-width: 1000px) {
  .pageTitle {
    font-size: 6.4vw;
  }
}

.media {
  display: flex;
  flex-direction: column;
  width: 250px;
  padding: 5px;
  margin: 10px 0px;
  background-color: #282c34;
  border-radius: 10px;
  position: relative;
  font-family: "Montserrat", sans-serif;
}

.media:hover {
  background-color: white;
  color: black;
}

@media (max-width: 550px) {
  .media {
    width: 46%;
  }
}

.poster {
  border-radius: 10px;
}

.title {
  width: 100%;
  text-align: center;
  font-size: 17px;
  padding: 8px 0;
}

.subTitle {
  display: flex;
  justify-content: space-between;
  padding-bottom: 3px;
  padding: 0 2px;
  padding-bottom: 3px;
}

.trending {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

